// Imports
import Vue from 'vue'
import Router from 'vue-router'
import { trailingSlash } from '@/util/helpers'
import {
  layout,
  route,
} from '@/util/routes'

Vue.use(Router)

const router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  scrollBehavior: (to, from, savedPosition) => {
    if (to.hash) return { selector: to.hash }
    if (savedPosition) return savedPosition

    return { x: 0, y: 0 }
  },
  routes: [
    
    layout('Default', [
      route('Inicio'),

      // Pages
      // Tables
      route('Usuarios', null, 'tables/usuarios'),
      route('Distrito', null, 'tables/distrito'),
      route('Profesion', null, 'tables/profesion'),
      route('Persona', null, 'tables/persona'),
      route('PersonaForm', null, 'tables/persona/form'),
      route('Kardex', null, 'tables/kardex'),
      route('KardexForm', null, 'tables/kardex/form'),
      route('KardexGestion', null, 'tables/kardex-gestion'),
      route('Marbete', null, 'tables/marbete'),
      route('MarbeteGestion', null, 'tables/marbete-gestion'),
      route('Posgrado', null, 'tables/posgrado'),
      route('PosgradoGestion', null, 'tables/posgrado-gestion'),
      route('Ingreso', null, 'tables/ingreso'),
      route('IngresoForm', null, 'tables/ingreso/form'),
      route('TipoIngreso', null, 'tables/tipoingreso'),
    ]),
    layout('Public', [
      route('Login', null, '/login'),
    ]),
  ],
})

router.beforeEach((to, from, next) => {
  return to.path.endsWith('/') ? next() : next(trailingSlash(to.path))
})

export default router
